<template>
          <div class="row">
          <div class="span-16-lg relative">

            <div class="relative ind-1 ratio-20-9 full-span-outer">
              <div class="full-span-inner">
                <slot></slot>
              </div>
            </div>
            
            <crosshair-background></crosshair-background>
            <span-background></span-background>

          </div>
        </div>
</template>

<script>
  import crosshair_background from '../atoms/crosshair_background'
  import span_background from '../atoms/span_background'

  export default {
    components: {
      'span-background': span_background,
      'crosshair_background': crosshair_background
    }
  }
</script>


<style scoped lang="scss">

  @import '../../styles/variables';

  .full-span-outer {
    display: flex;
  }

  .full-span-inner {
    position: relative;
    margin: auto;
    padding-top: 80px;
    padding-bottom: 80px;
    max-width: 100% * 5/8;
    @media screen and (max-width: $sm-max) {
      max-width: calc(100% - 24px - 24px - 12px); // additional 12 overall added to make less cramped
    }
  }

</style>



