<template>
  <div class="span-4-lg offset-1-lg span-14-md offset-1-md span-14-sm offset-1-sm span-14-xs offset-1-xs ratio-flip-card overflow-visible md-margin-top">

    <div class="flip-container">

      <input class="flip-indicator ind-3" type="checkbox"></input>

      <div class="flipper">

        <div class="front ind-2" front-card v-bind:style="front_styles">
          
          <!-- inner front content -->
          <div class="front-inner">
            <div class="svg-icon-background"><component v-bind:is="icon"></component></div>
            <div class="subheader upcase">Title</div>
          </div>

        </div>

        <div class="back">

          <!-- inner back content -->
          <div class="back-inner">
            
            
            <div class="subheader upcase"> 
              <div class="svg-icon-background svg-icon-background-small"><component v-bind:is="icon"></component></div> <span>Title</span>
            </div>

            <p>
              Ut gravida vel tortor eu maximus. Nunc facilisis et mi at laoreet. Quisque et luctus leo. Maecenas porta, nibh ac tristique aliquet, tellus leo vulputate risus, ut tempor nibh quam eu orci. Donec placerat condimentum nisi eget tincidunt. Nunc scelerisque ex eu lorem feugiat convallis. Curabitur eget nisl elit. Nullam sed imperdiet urna, non accumsan ligula. 
            </p>

          </div>

        </div>

        <crosshair-background></crosshair-background>

      </div>

    </div>

  </div>
</template>

<script>
  import crosshair_background from '../atoms/crosshair_background'
  import svg from '../svg/svg_main'
  import throttle from 'lodash.throttle' // for performance

  export default {
    props: ['icon'],
    components: {
      'crosshair-background': crosshair_background,
      ...svg.components
    },
    data () {
      return {
        front_styles: {
          'max-height': '100%'
        }
      }
    },
    methods: {
      // prevent all flip cards from assuming height of an overflowed card in row
      set_max_height () {
        let el = this.$el.querySelector('[front-card]')
        this.front_styles['max-height'] = el.offsetWidth * 4/3 + 'px'
      }
    },
    mounted () {
      this.set_max_height()
      window.addEventListener('resize', throttle(this.set_max_height, 1000/24))
    }
  }
</script>

<style scoped lang="scss">

  @import '../../styles/variables';

  //- col with rollover and 3d rotate -//
  // (see https://davidwalsh.name/css-flip#ieflip)

  // checkbox toggle
  .flip-indicator {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    &:hover + .flipper,  &:checked + .flipper{
      opacity: 1;
      .back, .front {
        border: 1px solid $blue;
      }
    }
    &:hover + .flipper {
      .crosshair-container {
        opacity: 1;
      }
    }
    &:checked + .flipper {
      .back {
        transform: rotateY(0deg);
        max-height: 150vh; // expand back panel to fit content
      }
      .front {
        transform: rotateY(180deg);
      }
    }
  }

  // perspective container
  .flip-container {
    perspective: 1200px;
    transform-style: preserve-3d;
  }

  // grow container to dynamically sized back content (safe to assume front will usually be shorter)
  .flip-container, .back { 
    position: relative;
  }

  // position front absolute so relatively-positioned back can remain in correct place (top 0 left 0)
  .front {
    position: absolute;
    display: flex;
    top: 0;
    left: 0;
    transform: rotateY(0deg);
  }

  .back {
    display: flex;
    max-height: 0px; // maintain 3-4 ratio when hidden
    transform: rotateY(-180deg);
    transition: max-height 1.5s;
  }

  .flip-container, .front, .back, .flipper {
    width: 100%;
    height: 100%;
  }
  
  /* flip speed goes here */
  .flipper {
    position: relative;
    opacity: 0.75;
    transition: 0.75s;
    transform-style: preserve-3d;
    .crosshair-container {
      opacity: 0;
    }
  }

  /* hide back of pane during swap */
  .front, .back {
    box-sizing: border-box;
    border: 1px solid transparent;
    background-color: $white;
    backface-visibility: hidden;
    transition: 0.75s;
    transform-style: preserve-3d;
  }


  // inner content

  .front-inner, .back-inner {
    position: relative;
    margin: auto;
  }

  .front-inner {
    text-align: center;
  }

  .back-inner {
    padding: 24px;
  }

  .svg-icon-background {
    position: relative;
    margin-bottom: 24px;
  }

  .svg-icon-background-small {
    display: inline-block;
    margin-right: 6px;
    transform: translateY(4px);
  }

  .md-margin-top {
    @media screen and (max-width: $md-max) {
      margin-top: 24px;
    }
  }

</style>

<style lang="scss">
  .svg-icon-background-small {
    svg {
      width: 24px;
      height: 24px;
    }
  }
</style>


