<template>
  <div class="progress-selector-wrapper">

    <input type="checkbox" class="progress-selector-indicator ind-2"></input>

    <div class="progress-selector-content ind-1 border-bg-rollover">
      <div class="p"><slot></slot></div>
      <arrow type="next"></arrow>
    </div>

    <div class="disable-next-progress-selector ind-3"></div>

  </div>
</template>

<script>
  import arrow from '../atoms/arrow'
  export default {
    components: {
      'arrow': arrow
    }
  }
</script>

<style scoped lang="scss">

  @import '../../styles/variables';
  
  //- progress selector -//

  .progress-selector-wrapper {
    position: relative;
    // DO NOT put a z-index on this.
  }

  .progress-selector-indicator {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
    &:hover, &:checked {
      + .progress-selector-content {
        opacity: 1;
        border: 1px solid $blue;
        + .crosshair-container {
          opacity: 1;
        }
      }
    }
    &:checked { // prevent unchecking
      pointer-events: none;
      ~ .disable-next-progress-selector {
        pointer-events: none; // enable checking next selector
      }
    }
  }

  .progress-selector-content {
    position: relative;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 60px;
    margin-bottom: 12px;
    border: 1px solid transparent;
    p, .p {
      margin-left: 24px;
    }
    .arrow-container {
      margin-right: 24px;
    }
  }

  .disable-next-progress-selector {
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 60px;
    cursor: not-allowed;
    width: 100%;
    height: 100vh;
    opacity: 0;
  }
</style>

<style lang="scss">
  // select arrow brackets outside of scope
  .progress-selector-indicator {
    &:hover + .progress-selector-content {
      // todo -> make this a mixin (reused across progress selector and button.vue)
      .arrow-container {
        .bracket-left {
          transform: translateX(-3px);
        }
        .bracket-right {
          transform: translateX(3px);
        }
      }
    }
  }
</style>



