import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const moduleA = {
  state: {
    count: 0
  },
  mutations: {
    increment (state) {
      state.count++
    }
  }
}

export default new Vuex.Store({
  // modules: {
  //   moduleA,
  // }
  state: {
    count: 0
  },
  mutations: {
    increment (state) {
      state.count++
    }
  }
})




