<template>
  <rollover-card v-bind:spanclass="span_computed" ratioclass="ratio-5-4-card">

    <div class="slide-outer">
      <div class="slide-content extra-max-width">

        <slot></slot>

        <div class="float-right">
          <nav-button type="share">share</nav-button>
        </div>

      </div>
    </div>

  </rollover-card>
</template>

<script>
  import rollover_card from '../molecules/rollover_card'
  import nav_button from '../molecules/button'

  export default {
    props: ['bump'],
    components: {
      'rollover-card': rollover_card,
      'nav-button': nav_button
    },
    computed: {
      span_computed () {
        if (this.bump.indexOf('down') !== -1) {
          return "span-6-lg offset-2-lg span-6-md offset-2-md span-14-sm offset-1-sm span-14-xs offset-1-xs " + this.bump
        }
        return "span-6-lg offset-1-lg span-6-md offset-1-md span-14-sm offset-1-sm span-14-xs offset-1-xs " + this.bump
      }
    }
  }
</script>

<style scoped lang="scss">
  @import '../../styles/variables';

  .bump-up {
    transform: translateY(-18px);
  }

  .bump-down {
    transform: translateY(18px);
  }

  .bump-up, .bump-down {
    @media screen and (max-width: $sm-max) {
      transform: none;
      margin-bottom: 36px;
    }
  }

  .slide-outer {
    display: flex;
  }

  .float-right {
    float: right;
  }

  // todo: import .slide-content class, reused here and on overflow_image.vue

  .slide-content {
    position: relative;
    margin: auto;
    width: 100%;
    padding-top: calc(100% * 1/8);
    padding-bottom: calc(100% * 1/8);
    max-width: calc((100% * 6/8) - 48px); // 48px to account for margin
    &.extra-max-width {
      @media screen and (max-width: $md-max) {
        max-width: calc(100% - 24px - 24px);
      }
    }
  }
</style>


