<template>
  <div class="navbar ind-4">
    <div class="navbar-inner">
      <nav-button type="back">back</nav-button>
      <nav-button type="next">next</nav-button>
    </div>
  </div>
</template>

<script>
  import button from '../molecules/button'

  export default {
    components: {
      'nav-button': button
    }
  }
</script>

<style scoped lang="scss">

  @import '../../styles/variables';

  .navbar {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 96px;
    background: $white-solid;
    box-shadow: 0 -2px 5px 0 rgba(0,0,0,0.05);
    @media screen and (max-width: $sm-max) {
      height: 60px;
    }
  }

  .navbar-inner {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    width: calc(100% - 80px - 80px);
    height: 100%;
    @media screen and (max-width: $sm-break) {
      width: calc(100% - 24px - 24px);
    }
  }

</style>


